














.welcome{
  max-width: 400px;
  margin-top: 100px;
}
.welcome h2{
  font-size: 3em;
}
.welcome button{
  margin: 30px auto;
}

  


.chat h2 {
    font-size: 2.6rem;
    margin-bottom: 40px;
}
.chat.time {
    font-size: 0.8rem ;
   display: block;
}
.chat span {
    font-size: 1.4rem;
}
.messages{
    max-height: 300px;
    overflow:auto;
}
.messages::-webkit-scrollbar{
    width: 3px;
}
.messages::-webkit-scrollbar-track{
    background: #ddd;
}
.messages::-webkit-slider-thumb{
    background: #aaa;
}














































